.sidemenu{
    position: fixed;
    background: #fff;
    width: 282px;
    height: 100%;
    /* color: #fff; */
    box-sizing: border-box;
    padding: 30px 20px;
    transition: width .2s ease-in;
}

.bi.bi-grid-fill{
    color: #E2698D;
}

h4.main-header{
    color: #828282;
    font-family: "Trueno";
    font-size: 14px;
    font-weight: 500;
    margin: 30px 0;
    text-decoration: left;
}

.sidemenu.inactive {
    width: 80px;
}

.sidemenu .top-section {
    position: relative;
}

.sidemenu .top-section .logo{
    width: "50px";
    height: "50px";
    overflow: hidden;
    display: inline-block;
}

.sidemenu .top-section .logo img{
    max-width: 100%;
    max-height: 100%;
}

.sidemenu.inactive .top-section .logo img{
    width: 40px;
    height: 40px;
}

.sidemenu .top-section .toggle-menu-btn{
    color: #333;
    font-family: "Trueno";
    font-size: 20px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    cursor: pointer;
}

.sidemenu.inactive .top-section .toggle-menu-btn{
    right: -50px;
}

.divider{
    width: 100%;
    height: 1px;
    border-radius: 1px;
    background: #333;
}

.sidemenu .main-menu .main-item{
    color: #7C7F93;
    text-decoration: none;
    font-size: 14px;
    display: block;
    font-family: "Trueno";
    font-weight: 300;
    cursor: pointer;
    margin-left: 30px;
}

.sidemenu .main-menu .main-item.inactive{
    margin-left: 0px;
    font-size: 18px;
}

.sidemenu .main-menu .main-item span{
    font-family: "Trueno";
    font-weight: 500;
    position: absolute;
    display: inline-block;
    line-height: 30px;
    opacity: 1;
    transition: visibility 0s, opacity .5s ease-in;
}

.sidemenu .main-menu .main-item span:hover{
    color: #E2698D;
    font-family: "Trueno";
    font-weight: 700;
}

.sidemenu.inactive .main-menu .main-item span{
    /* display: none; */
    /* visibility: hidden; */
    opacity: 0;
    width: 0;
    height: 0;
    overflow: hidden;
}

.sidemenu .main-menu .main-item .main-icon {
    display: inline-block;
    width: 40px;
    height: 40px;
    font-family: "Trueno"; 
    font-size: 20px;
    text-align: center;
}

.sidemenu .main-menu .main-item.inactive .main-icon {
    font-size: 20px;
}

