#main-slider {
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
}

.slider {
    width: 921px;
    height: 100%;
    white-space: nowrap;
    overflow-x: scroll;
    scrollbar-width: none;
    scroll-behavior: smooth;
}

.slider.collapse {
    width: 1100px;
}

#slider::-webkit-scrollbar{
    display: none;
}

.content {
    margin-bottom: 12px;
    color: #727274;
}

.content-details h3 {
    font-family: "Trueno";
    font-size: 32px;
    font-weight: 700;
}

.content-details p {
    font-family: "Trueno";
    font-size: 16px;
    font-weight: 300;
}

.slider-icon.left, .slider-icon.right{
    background: #fff;
    color: #000;
    border-radius: 100%;
    font-size: 14px;
    position: absolute;
    padding:6px;
    opacity: 0.5;
    box-shadow: 2px 2px 2px 2px rgb(0 0 0 / 12%);
    cursor: pointer;
}

.slider-icon.left:hover, .slider-icon.right:hover{
    opacity: 1;
}

.slider-icon.left {
    left: 50;
}

.slider-icon.right {
    right: 0;
}

.slider-card {
    width: 300px;
    height:173px;
    background: #fff;
    border-radius: 12px;
    display: inline-block;
    margin-left: 12px;
    margin-right: 12px;
    box-shadow:
0 5px 10px rgba(154,160,185,0.05),
0 15px 40px rgba(166,173,201,0.2);
}

.slider-content {
    margin-top: 15px;
    width: 100%;
    height: 100%;
    padding: 10px;
    display: flex;
    align-items: left;
    flex-direction: column;
}

.slider-header {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.slider-header .icon {
    background: #C2EFF0;
    padding: 14px;
    border-radius: 100%;
    margin-right: 10px;

}

.slider-header  h3{
    font-family: "Trueno";
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
}

.slider-body {
    width: 100%;
    height: 100%;
    /* padding-left: 150px; */
}

.slider-body h3 {
    color: #000;
    font-size: 24px
}

.slider-body .loader{
    height: 5px;
    width: 270px;
    border: .1px solid #a3a3a3;
    border-radius: 2px;
    margin-top: 14px;
}

.slider-body .loader .complete{
    background: #265859;
    height: 4px;
    width: 112.26px;
    border-radius: 2px;
}