.home {
    margin-bottom: 40px;
}

.home .hero {
    display: flex;
    gap: 10px;
    align-items: center;
    margin-bottom: 12px;
    color: #727274;
}

.hero-details p{
    font-family: "Trueno";
    font-size: 16px;
    font-weight: 300;
}

.hero-details h3{
    font-family: "Trueno";
    font-size: 20px;
    font-weight: 700;
}

.account-details-wrapper {
    background-image: url("../assets/wallpaper.png");
    background-repeat: no-repeat;
    border-radius:12px;
    width: 100%;
    height: 200px;
    color: #fff;
    display: flex;
    align-items: center;
}

/* .account-details-wrapper.collapse {
    width: 1090px;
    padding: 12px;
} */

.account-details-contents {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 55px;
}

.account-details-content1 {
    margin: 0 40px;
}

.account-details-content1 p {
    font-family: "Trueno"; 
    font-size: 14px;
    margin-bottom: 12px;
}

.account-details-content1 h4 {
    font-size: 20px;
}